import jsonp from 'assets/js/jsonp';
import {jsonpOptions} from './config';

export const getProductDetail = id => {
  const url = 'https://unszacs.m.taobao.com/h5/mtop.taobao.detail.getdetail/6.0/';
  const params = {
    api: 'mtop.taobao.detail.getdetail',
    ttid: '2017@taobao_h5_6.6.0',
    data: `{"itemNumId":"${id}"}`,
    appKey: 12574478,
    dataType: 'jsonp',
    type: 'jsonp'
  };
  return jsonp(url, params, jsonpOptions).then(res => {
    let shuju = res.data;
    if (res.ret[0] === 'SUCCESS::') {
      let data = {};
      const apiStack = JSON.parse(res.data.apiStack[0].value);
      data.content = {};
      data.content.priceText = apiStack.priceSectionData.price.priceText;// 
      data.content.soldCount = apiStack.vertical.jhs.soldCount;// 
      data.content.title = apiStack.item.title;// 
      data.content.postage = apiStack.delivery.postage;// 
      data.content.from = apiStack.delivery.from;// 
      data.content.sellCount = apiStack.item.sellCount;// 
      data.content.sellerNick = shuju.seller.sellerNick;// 
      data.content.shopName = shuju.seller.shopName;// 
      data.content.shopIcon = shuju.seller.shopIcon;// 
      data.content.totalCount = shuju.rate.totalCount;// 
      data.content.keywords = shuju.rate.keywords;// 
      // [{word,count},{},{}]
      data.content.userName = shuju.rate.rateList[0].userName;// 
      data.content.dateTime = shuju.rate.rateList[0].dateTime;// 
      data.content.content = shuju.rate.rateList[0].content;// 
      data.content.headPic = shuju.rate.rateList[0].headPic;// 
      data.content.creditIcon = shuju.seller.creditLevelIcon; // 
      data.content.evaluates = shuju.seller.evaluates;// 
      // [{title,score},{},{}]
      data.slider = apiStack.item.images;// 
      return data;
    }

    throw new Error('!');
  }).catch(err => {
    if (err) {
      console.log(err);
    }
  }).then(res => {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve(res);
      }, 1000);
    });
  });
};



